body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
}

.contenedor {
  width: 95%;
  max-width: 1000px;
  margin: 0 auto;
}

.contenedor-md {
  width: 95%;
  max-width: 800px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

main {
  flex: 1;
}

.mt-2 {
  margin-top: 2rem;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1, h2 {
  font-weight: 400;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 300;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

.header {
  padding: 4rem 0;
  background-color: #6D94FD;
}
.header.imagen {
  background-image: url(../img/header.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.imagen {
    min-height: 60rem;
  }
}
.header__titulo {
  line-height: 2;
  text-align: center;
  color: #FFFFFF;
  font-weight: bold;
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .header__titulo {
    margin-top: unset;
    text-align: left;
    max-width: 60rem;
    font-size: 4rem;
  }
}
.header__contenido {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .header__contenido {
    min-height: 50rem;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
  }
}

.logo {
  width: 30rem;
}
@media (min-width: 768px) {
  .logo {
    max-width: 45rem;
  }
}

.nav {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 0;
  transition: height 0.3s linear;
}
@media (min-width: 768px) {
  .nav {
    flex-direction: row;
    visibility: visible;
    opacity: 1;
  }
}
.nav__enlace {
  display: block;
  margin: 1rem 0;
  font-weight: bold;
  font-size: 1.9rem;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .nav__enlace {
    display: inline-block;
    margin: unset;
    margin-right: 2rem;
  }
}
.nav__enlace:hover {
  color: #78E0FA;
}

.mobil_menu {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .mobil_menu {
    display: none;
  }
}
.mobil_menu img {
  width: 4rem;
  height: 4rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.dark-mode-btn {
  width: 4rem;
  height: 4rem;
  margin: 1rem 0;
  filter: invert(100%);
}
@media (min-width: 768px) {
  .dark-mode-btn {
    margin: 0;
    margin-right: 1.5rem;
  }
}
.dark-mode-btn:hover {
  cursor: pointer;
}

.footer {
  background-color: #6D94FD;
}
.footer__contenido {
  padding-top: 2rem;
}
.footer .nav {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer__copy {
  color: #FFFFFF;
  font-size: 2rem;
  text-align: center;
  padding: 1rem;
  margin: 0;
  font-weight: bold;
}

.categorias {
  margin: 2rem 0;
}
.categorias__titulo {
  text-align: center;
  color: #011e6a;
  margin: 2rem 0;
  text-transform: uppercase;
  font-weight: 300;
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .categorias__titulo {
    font-size: 3rem;
  }
}
@media (min-width: 768px) {
  .categorias__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.categoria {
  text-align: center;
  padding: 1rem;
}
@media (min-width: 768px) {
  .categoria {
    padding: 2rem;
  }
}
.categoria__maks {
  overflow: hidden;
}
.categoria__img {
  margin: 0 auto;
  width: 12rem;
  height: 12rem;
}
.categoria__img:hover {
  animation-name: rotarIcono;
  animation-duration: 2s;
  animation-timing-function: ease-in-out;
}
.categoria__titulo {
  margin: 2rem 0;
  color: #033ace;
  text-transform: uppercase;
  font-size: 2.3rem;
}

@keyframes rotarIcono {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(90deg);
  }
  50% {
    transform: rotate(180deg);
  }
  75% {
    transform: rotate(270deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.propiedades {
  margin: 2rem 0;
}
.propiedades__titulo {
  text-align: center;
  margin: 2rem 0;
  color: #011e6a;
  text-transform: uppercase;
  font-weight: 300;
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .propiedades__titulo {
    font-size: 3rem;
  }
}
.propiedades__grid {
  display: grid;
  grid-template-columns: repeat(4, 33rem);
  gap: 4rem;
  overflow-y: hidden;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}
.propiedades__grid.extended {
  grid-template-columns: repeat(6, 33rem);
}
.propiedades__grid > * {
  scroll-snap-align: center;
}
@media (min-width: 768px) {
  .propiedades__grid {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
    overflow-x: hidden;
  }
  .propiedades__grid.extended {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.propiedad__maks {
  overflow: hidden;
}
.propiedad__img {
  transition: all 0.3s;
}
.propiedad__img:hover {
  transform: scale(1.2);
}
.propiedad__contenido {
  background-color: #78E0FA;
  padding: 0 2rem 3rem 2rem;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  border-bottom-right-radius: 30px;
  border-bottom-left-radius: 30px;
}
.propiedad__titulo {
  margin-top: 0;
  padding: 1.5rem 0;
  text-align: center;
  font-weight: bold;
  color: #FFFFFF;
  background-color: #011e6a;
}
.propiedad__descripcion {
  text-align: justify;
  margin: 0;
  padding: 2rem 0;
}
.propiedad__precio {
  text-align: center;
  font-weight: bold;
  color: #011037;
  font-size: 2.4rem;
  margin-bottom: 1rem;
  margin-top: 0;
}
.propiedad__precio span {
  color: #011e6a;
}
.propiedad__iconos {
  display: flex;
  justify-content: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .propiedad__iconos {
    gap: 3rem;
  }
}
.propiedad__boton {
  display: block;
  background-color: #011e6a;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
}
@media (min-width: 768px) {
  .propiedad__boton {
    width: auto;
  }
}
.propiedad__boton:hover {
  background-color: #011037;
}

.icono {
  display: flex;
  align-items: center;
}
.icono__img {
  width: 5rem;
  height: 4rem;
}
.icono__cantidad {
  margin-left: 1rem;
  font-weight: bold;
}
@media (min-width: 1024px) {
  .icono__cantidad {
    margin-left: 2rem;
  }
}

.contactanos {
  margin: 2rem 0;
  height: 30rem;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 100%), url("../img/encuentra.webp");
  color: #FFFFFF;
  background-position: center center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
.contactanos__titulo {
  text-align: center;
  font-weight: bold;
}
.contactanos__descripcion {
  text-align: center;
}
.contactanos__boton {
  background-color: #011e6a;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
}
@media (min-width: 768px) {
  .contactanos__boton {
    width: auto;
  }
}
.contactanos__boton:hover {
  background-color: #011037;
}

.principal {
  padding: 2rem 0;
}
@media (min-width: 768px) {
  .principal {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 3rem;
  }
}

.blogs__titulo {
  text-align: center;
  color: #011e6a;
  font-weight: 300;
  margin-bottom: 2rem;
  font-size: 2.5rem;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .blogs__titulo {
    font-size: 3rem;
  }
}

.blog {
  background-color: #f5f5f5;
  display: grid;
  border: 0.1rem outset #1C6EA4;
  border-radius: 4px 8px 17px 16px;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .blog {
    grid-template-columns: 2fr 3fr;
    column-gap: 2rem;
  }
}
.blog__maks {
  overflow: hidden;
}
.blog__img {
  transition: all 0.3s;
  height: 100%;
  width: 100%;
}
.blog__img:hover {
  transform: scale(1.2);
}
.blog__descripcion {
  padding: 1rem 1rem 1rem 0;
}
.blog__titulo {
  padding: 1rem 0;
  color: #045a6f;
  font-weight: bold;
  text-align: center;
}
.blog__titulo::after {
  content: "";
  display: block;
  width: 20rem;
  height: 0.6rem;
  background-color: #022c9c;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .blog__titulo::after {
    margin: 0;
  }
}
@media (min-width: 768px) {
  .blog__titulo {
    text-align: left;
  }
}
.blog__autor {
  margin: 0.8rem 0;
  text-align: center;
}
.blog__autor span {
  color: #045a6f;
  font-weight: bold;
}
@media (min-width: 768px) {
  .blog__autor {
    text-align: left;
  }
}
.blog__texto {
  text-align: justify;
}

.testimonial__titulo {
  padding: 1rem 0;
  color: #045a6f;
  font-weight: 300;
  font-size: 2.5rem;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .testimonial__titulo {
    font-size: 3rem;
  }
}
.testimonial__contenido {
  background-color: #6DFDCA;
  padding: 4rem 5rem;
  border-radius: 3rem;
  position: relative;
}
.testimonial__contenido::before {
  position: absolute;
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  display: block;
  width: 5rem;
  height: 5rem;
  top: 3rem;
  left: 1rem;
}
.testimonial__texto {
  text-align: justify;
  font-size: 2rem;
}
.testimonial__autor {
  font-size: 2rem;
  margin-top: 2rem;
  text-align: right;
  font-weight: bold;
}

.nosotros {
  margin-top: 2rem;
}
.nosotros__titulo {
  text-align: center;
  color: #011e6a;
  margin: 2rem 0;
  text-transform: uppercase;
  font-weight: 300;
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .nosotros__titulo {
    font-size: 3rem;
  }
}
@media (min-width: 768px) {
  .nosotros__grid {
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 2rem;
  }
}
.nosotros__texto h3 {
  text-align: center;
  color: #033ace;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .nosotros__texto h3 {
    margin-top: 0;
  }
}
.nosotros__texto p {
  text-align: justify;
}

.contacto__titulo {
  text-align: center;
  color: #011e6a;
  margin: 2rem 0;
  text-transform: uppercase;
  font-weight: 300;
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .contacto__titulo {
    font-size: 3rem;
  }
}
.contacto__subtitulo {
  text-align: center;
  color: #011e6a;
  font-weight: 300;
  font-size: 2.5rem;
  margin: 2rem 0;
}

.formulario legend {
  text-transform: uppercase;
  font-weight: bold;
  color: #000a0d;
}
.formulario fieldset {
  border: 0.2rem solid #6D94FD;
  margin-bottom: 1.5rem;
}
.formulario fieldset:last-of-type {
  margin-bottom: 0;
}
.formulario p {
  font-size: 1.4rem;
  margin: 0;
  color: #292929;
}
.formulario textarea {
  display: block;
  width: 100%;
  resize: none;
  border: 1px solid #767676;
}

.campo {
  margin-bottom: 1.5rem;
}
.campo:last-of-type {
  margin-bottom: 0;
}
.campo__label {
  text-transform: uppercase;
  color: #045a6f;
  font-weight: bold;
}
.campo__input {
  display: block;
  width: 100%;
  padding: 1rem;
  background-color: #f5f5f5;
  border: none;
  border-radius: 1rem;
}
.campo__radios {
  display: flex;
  column-gap: 1.5rem;
  margin-bottom: 1rem;
}
.campo__radio {
  display: flex;
  align-items: center;
  column-gap: 1rem;
}

.btn-enviar {
  text-align: center;
  background-color: #6D94FD;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  margin: 2rem 0;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .btn-enviar {
    width: auto;
  }
}
.btn-enviar:hover {
  background-color: #3b6ffc;
}
@media (min-width: 768px) {
  .btn-enviar {
    display: inline-block;
  }
}

.dark-mode {
  background-color: #292929;
}
.dark-mode .header {
  background-color: #000503;
}
.dark-mode h2,
.dark-mode h3 {
  color: #FFFFFF;
}
.dark-mode p {
  color: #f5f5f5;
}
.dark-mode .propiedad__titulo {
