@use 'variables' as v;
html {
    font-size: 62.5%;
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    font-family: v.$fuentePrincipal;
    font-size: 1.6rem;
    font-weight: 300;
}

.contenedor {
    width: 95%;
    max-width: 1000px;
    margin: 0 auto;
}

.contenedor-md {
    width: 95%;
    max-width: 800px;
    margin: 0 auto;
}

img {
    max-width: 100%;
    display: block;
}

ul {
    padding: 0;
    list-style: none;
    margin: 0;
}

main {
    flex: 1;
}

.mt-2 {
    margin-top: 2rem;
}